import * as yup from "yup";


const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/


export const userSchema = yup.object().shape({
  hoTen: yup
    .string()
    .min(2, " di ti thiu h tn l 2 k t")
    .max(50, " di ti thiu h tn l 50 k t")
    .matches(/^[A-Z a-z]+$/, "H tn khng c cha du")
    .trim()
    .required("Vui lng nhp h tn"),
  matKhau: yup
    .string()
    .min(8, " di mt khu ti thiu l 8 k t")
    .trim()
    .required("Vui lng nhp mt khu"),
  email: yup
    .string()
    .trim()
    .required("Vui lng nhp email")
    .email("Vui lng nhp ng nh dng email!"),
  soDt: yup
    .string()
    .matches(phoneRegExp, 'S in thoi ny khng tn ti')
    .min(10, "S in thoi phi l 10 s")
    .max(10, "S in thoi phi l 10 s")
    .required("Vui lng nhp s in thoi"),
});

export const addUserSchema = yup.object().shape({
  hoTen: yup.string().trim().required("Vui lng nhp h tn"),
  taiKhoan: yup.string().trim().required("Vui lng nhp ti khon"),
  matKhau: yup.string().trim().required("Vui lng nhp mt khu"),
  email: yup
    .string()
    .trim()
    .required("Vui lng nhp email")
    .email("Vui lng nhp ng nh dng email!"),
  soDt: yup
    .string()
    .matches(phoneRegExp, 'S in thoi ny khng tn ti')
    .min(10, "S in thoi phi l 10 s")
    .max(10, "S in thoi phi l 10 s")
    .required("Vui lng nhp s in thoi"),
  maLoaiNguoiDung: yup
    .string()
    .trim()
    .required("Vui lng chn loi ngi dng"),
});

export const signInSchema = yup.object().shape({
  taiKhoan: yup.string().trim().trim().required("Vui lng nhp ti khon"),
  matKhau: yup.string().trim().required("Vui lng nhp mt khu"),
});
