import * as yup from "yup";

const passwordExpression = "^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{8,}$";
const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/;

export const signupSchema = yup.object().shape({
  taiKhoan: yup.string().trim().required("Vui lng nhp ti khon"),
  matKhau: yup.string().trim().required("Vui lng nhp mt khu").matches(passwordExpression,"Mt khu ti thiu 8 k t, bao gm ch in hoa v s"),
  xacNhanMatKhau: yup
    .string()
    .trim()
    .required("Vui lng xc nhn mt khu")
    .when("matKhau", {
      is: (val) => (val && val.length > 0 ? true : false),
      then: yup
        .string()
        .oneOf([yup.ref("matKhau")], "Mt khu xc nhn khng khp"),
    }),
  email: yup
    .string()
    .trim()
    .required("Vui lng nhp email")
    .email("Vui lng nhp ng nh dng email!"),
  soDt: yup
  .string()
  .matches(phoneRegExp, 'S in thoi ny khng tn ti')
  .min(10, "S in thoi phi l 10 s")
  .max(10, "S in thoi phi l 10 s")
  .required("Vui lng nhp s in thoi"),
  hoTen: yup.string().trim().required("Vui lng nhp h tn"),
});
