import * as yup from "yup";

export const addNewFilmSchema = yup.object().shape({
  tenPhim: yup
  .string()
  .min(2, " di ti thiu tn phim l 2 k t")
  .max(30, " di ti thiu tn phim l 30 k t")
  .trim()
  .required("Vui lng nhp tn phim"),
  trailer: yup
  .string()
  .min(2, " di ti thiu trailer l 20 k t")
  .trim()
  .required("Vui lng thm trailer"),
  moTa: yup
  .string()
  .min(2, " di ti thiu m t l 20 k t")
  .trim()
  .required("Vui lng thm m t phim"),
  hinhAnh: yup.string().required("Vui lng nhp lin kt nh"),
});

export const editFilmSchema = yup.object().shape({
  tenPhim: yup
  .string()
  .min(2, " di ti thiu tn phim l 2 k t")
  .max(50, " di ti a tn phim l 50 k t")
  .trim()
  .required("Vui lng nhp tn phim"),
  trailer: yup
  .string()
  .min(2, " di ti thiu trailer l 2 k t")
  .max(100, " di ti a trailer l 100 k t")
  .trim()
  .required("Vui lng thm trailer"),
  moTa: yup
  .string()
  .min(2, " di ti thiu m t l 2 k t")
  .max(1500, " di ti a m t l 1500 k t")
  .trim()
  .required("Vui lng thm m t phim"),
});